@import url(https://fonts.googleapis.com/css2?family=Eater&family=PT+Serif&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  line-height: normal;
  background-color: #202020;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width : 5px;
  background-color: #c4c2c2;
}

::-webkit-scrollbar-thumb {
  width: 5px;
  background-color: #333;
  border-radius: 10px;

}

nav {
  width: 100vw;
  height: 10vh;
  background-color: #40407a;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content:center;
          justify-content:center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.6rem;
  color: antiquewhite;
  font-weight: bold;
  letter-spacing: 3px;
}

.name {
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 30px;
  -webkit-justify-content:space-evenly;
          justify-content:space-evenly;
}

input {
  width: 35vw;
  height: 1.5cm;
  background-color: transparent;
  border: none;
  color: #d8d8d8;
  font-size: 1.1rem;
  border-bottom: 3px solid #9b59b6;
  text-align: center;
}
input:focus{
  transition: .5s;
  box-shadow: 0px 0px 10px 6px #9b59b6;
  outline: none;
}


section {
  margin: 3%;
  max-zoom: 3.2px;
  margin-left: 10%;
  width: 78vw;
  max-height: 100cm;
  display: grid;
  box-shadow: -4px -5px 15px 3px #9b59b6;
  background-color: #1d1d1d;
  padding: 15px;
  grid-template-columns: auto auto auto;
}

section img {
  width: 400px;
  height: 300px;
 
}



section div .starting{
  position: absolute;
  top: 307px;
  left: 390px;
  font-size: 1.3rem;
  font-weight: bold;
  color: #0e0e0e;
  font-family: consolas;
  -webkit-transform: rotateZ(74deg);
          transform: rotateZ(74deg);
}
section div .second{
  position: absolute;
  top: 463px;
  color: #c9c9c9;
  left: 46%;
  font-size: 1.4rem;
  font-weight: bold;
  font-family: consolas;
}
section div .everything{
  position: absolute;
  top: 399px;
  left: 68%;
  font-size: 1.1rem;
  font-weight: bold;
  font-family: consolas;
  color: white;
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
}
section div .Stir{
  position: absolute;
  top: 696px;
  left: 220px;
  font-size: 1.8rem;
  font-weight: bold;
  font-family: consolas;
  color: #c4c2c2;
  letter-spacing: 4px;
  -webkit-transform: rotateZ(20deg);
          transform: rotateZ(20deg);
}

section div .break{
  position: absolute;
  top: 586px;
  left: 37%;
  font-size: 1.8rem;
  font-weight: bold;
  font-family: consolas;
  color: #c4c2c2;
  letter-spacing: 4px;
  
}
section div .chemical{
  position: absolute;
  top: 630px;
  left: 70%;
  font-size: 2.1rem;
  text-shadow: 1px 1px rgb(197, 134, 134);
  font-family: consolas;
  color: #222222;
  letter-spacing: 4px;
  font-family: 'Eater', cursive;

  
}

.toast {
  
  width: 35vw;
}
section div .final{
  position: absolute;
  top: 880px;
  left: 76%;
  font-size: 1.6rem;
  font-family: consolas;
  color: #222222;
  letter-spacing: 4px;

  
}


.pulseBtn {
    font-size: 1.1rem;
    background-color: #5758BB;
    color: white;
    width: 4cm;
    height: 1.2cm;
    border-radius: 4px;
    box-shadow: 0 0 0 0 #5758BB;
    -webkit-animation: pulse 1.75s infinite cubic-bezier(0.66, 0, 0, 1);
            animation: pulse 1.75s infinite cubic-bezier(0.66, 0, 0, 1);
}

@-webkit-keyframes pulse {
    to {
      box-shadow: 0 0 0 30px rgba(230, 237, 255, 0);
    }
}

@keyframes pulse {
    to {
      box-shadow: 0 0 0 30px rgba(230, 237, 255, 0);
    }
}


@media only screen and (max-width: 500px) {
  .input {
     font-size: .9rem;
  }
  nav {
     font-size: 1rem;
  }
   section {
      margin-top: 30px;
      display: grid;
      grid-template-columns: auto;
      box-shadow: 1px 1px 10px 2px #9b59b6 ;
   }

   section div .starting {
      position: absolute;
      top: 280px;
      left: 280px;
   }

   section div .second {
      position: absolute;
      top: 97%;
   }

   section div .everything {
      position: absolute;
      top: 128%;
      left: 25%;
   }
   section div .Stir {
      position: absolute;
      top: 165%;
      left: 18%;
   }
   section div .break {
      position: absolute;
      top: 191%;
      left: 15%;
   }
   section div .chemical {
      position: absolute;
      top: 237%;
      left: 26%;
   }
   section div .final {
      position: absolute;
      top: 347%;
      left: 56%;
   }

}
